body {
    width: 100vw;
    height: auto;

    margin: 0;
    padding: 0;

    overflow: hidden;

    font-family: 'Barlow', sans-serif;
}

.no-drag {
    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -webkit-user-select: none;
    user-select: none;
}

input[type='text'],
input[type='password'],
input[type='email'],
input[type='submit'],
input[type='button'],
textarea {
    -webkit-appearance: none;
}

::-webkit-scrollbar {
    -webkit-appearance: none;
}

::-webkit-scrollbar:vertical {
    width: 15px;
}

::-webkit-scrollbar:horizontal {
    height: 15px;
}

::-webkit-scrollbar-track {
    background: #272727;
}

::-webkit-scrollbar-thumb {
    background: #474747;
}

::-webkit-scrollbar-thumb:hover {
    background: #555555;
}

body.react-confirm-alert-body-element {
    overflow: hidden;
}

.react-confirm-alert-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
    background: #202020;
    opacity: 0.99;
}

.react-confirm-alert-body {
    width: 20rem;
    height: 3rem;
    padding: 1rem;

    position: absolute;
    top: calc(50% - 2.5rem);
    left: calc(50% - 11rem);

    color: #808080;
    font-size: 0.75rem;
    font-weight: 500;

    background: #404040;
    border: solid 0.1rem #202020;
    border-radius: 0.5rem;
}

.react-confirm-alert-button-group {
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    justify-content: flex-start;
    margin-top: 0.85rem;
}

.react-confirm-alert-button-group > button {
    width: auto;
    height: auto;

    margin-right: 0.5rem;
    padding: 0.25rem 0.5rem;

    color: #aaaaaa;
    font-size: 0.5rem;
    font-weight: 500;

    background: #303030;
    border: none;
    border-radius: 0.25rem;

    cursor: pointer;
    outline: none;
}

@-webkit-keyframes react-confirm-alert-fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes react-confirm-alert-fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

