    background-color: var(--color-70);
}

.searchBar-button {
    width: 4rem;
    height: 100%;

    background-color: var(--color-50);
    border: none;
    border-radius: 0 0.25rem 0.25rem 0;
    cursor: pointer;
    outline: none;

    -webkit-transition: 0.5s;

    transition: 0.5s;
}

.searchBar-button:hover {
    background-color: var(--color-40);
}

